@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP&family=Open+Sans&display=swap);
html,body{color:#fafafa;padding:0;margin:0;height:100%;width:100%;background-image:linear-gradient(45deg, rgba(0,0,0,0.4) 25%, transparent 25%),linear-gradient(-45deg, rgba(0,0,0,0.4) 25%, transparent 25%),linear-gradient(45deg, transparent 75%, rgba(0,0,0,0.4) 75%),linear-gradient(-45deg, transparent 75%, rgba(0,0,0,0.4) 75%);background-size:5px 5px;background-position:0 0,0 2.5px,2.5px -2.5px,-2.5px 0;background-color:#2b2b2b54;font-family:'Open Sans', sans-serif}#root{width:100%;height:100%}

.view{width:100%;height:100%}.canvas{z-index:-1;position:absolute;width:100%;height:100%}.side-nav{width:25%;height:100%;background-color:#1f1c1c62;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:space-evenly;justify-content:space-evenly}.content{width:75%;padding:10%;box-sizing:border-box}.content-header{font-size:32px;margin-top:5rem}.content-text{width:100%;margin-top:3rem;margin-bottom:2rem;padding-left:15%;padding-right:15%;padding-top:1rem;padding-bottom:1rem;font-size:13px;background-color:#2b2b2b67;box-sizing:border-box;text-align:center;font-family:"Noto Sans JP", sans-serif}.btn{width:10.5rem;height:3rem;margin:1rem;background-color:#2b2b2bf5;outline:none;border:none;color:#fafafa;cursor:pointer}.btn-round{border-radius:20px}.btn-reverse{background:#ECE9E6;background:linear-gradient(to bottom, #fff, #ECE9E6);color:#2b2b2b;font-size:12px}.center{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;-webkit-flex-direction:column;flex-direction:column}.center-row{-webkit-flex-direction:row;flex-direction:row}.body{width:50rem;background-color:#2b2b2b80;transition:height 1s}.active{background-color:#fafafa;color:#2b2b2b}.bordered{border-image-source:url("http://i.stack.imgur.com/s2CAw.png");border-image-slice:1;border-image-width:5px}.fade-in-top{-webkit-animation:fade-in-top 1s cubic-bezier(0.39, 0.575, 0.565, 1) both;animation:fade-in-top 1s cubic-bezier(0.39, 0.575, 0.565, 1) both}@-webkit-keyframes fade-in-top{0%{-webkit-transform:translateY(-50px);transform:translateY(-50px);opacity:0}100%{-webkit-transform:translateY(0);transform:translateY(0);opacity:1}}@keyframes fade-in-top{0%{-webkit-transform:translateY(-50px);transform:translateY(-50px);opacity:0}100%{-webkit-transform:translateY(0);transform:translateY(0);opacity:1}}.maxPdf{width:700px;height:575px;text-align:center;line-height:575px;overflow:hidden}.videoPlayer{padding:1rem;margin:1rem;background-color:#2b2b2b7e;border-radius:10px;border:1px solid #2b2b2b}

